.navbar-help {
  background-color: #ED462F; }
  .navbar-help span {
    color: #FDF733; }

.navbar-help, .navbar-basic {
  width: 100%; }

@media (min-width: 768px) {
  .navbar-basic {
    display: flex; }
    .navbar-basic div {
      text-align: center; }
  .navbar-help {
    display: none; } }

@media (min-width: 1024px) {
  .navbar {
    grid-row: 1 !important;
    display: unset !important; }
    .navbar-basic {
      justify-content: space-around; }
      .navbar-basic div {
        text-align: right; } }
