.topbar-legal {
  border-right: 1px solid lightgray; }

.topbar a:hover {
  text-decoration: none; }

@media (min-width: 480px) {
  .topbar {
    display: none; } }

@media (min-width: 768px) {
  .topbar {
    display: flex;
    justify-content: flex-end; } }

@media (min-width: 1024px) {
  .topbar {
    padding-right: 60px !important; } }
